import React from 'react'
import ReactECharts from "echarts-for-react";

const Chart = ({ report }) => {
    const option = {
        tooltip: {
            trigger: "item",
        },

        legend: {
            top: "5%",
            left: "center",
        },
        series: [
            {
                name: "Report",
                type: "pie",
                radius: ["40%", "70%"],
                avoidLabelOverlap: false,
                itemStyle: {
                    borderRadius: 10,
                    borderColor: "#fff",
                    borderWidth: 2,

                },
                label: {
                    show: false,
                    position: "center",
                },
                emphasis: {

                    label: {
                        show: true,
                        fontSize: "20",
                        fontWeight: "bold",
                    },
                },
                labelLine: {
                    show: false,
                },
                data: [
                    { value: report && report.videoUpload, name: "Video Uploads" },
                    { value: report && report.videoDownload, name: "Video Downloads" },
                    { value: report && report.location, name: "Locations" },
                    { value: report && report.student, name: "Students" },
                    { value: report && report.admin, name: "Admin" },
                    { value: report && report.superAdmin, name: "Super Admin" },
                ],
            },
        ],
    };
    return (
        <div>
            <ReactECharts option={option} style={{ height: '500px', width: '100%' }} />
        </div>
    )
}

export default Chart
